/* --------------------------------------------------- mobile: up to 367px --------------------------------------------------- */
@media screen and (max-width: 376px) {
  .image,
  .img {
    display: block;
    height: auto;
  }
  .landingtext {
    font-size: 26px;
    color: var(--dgreen);
    font-weight: 700;
  }
  .landingtexth1 {
    font-size: 50px;
  }
  .label {
    font-size: 30px;
  }
  .checkbox {
    font-size: 20px;
  }
  .tabs {
    font-size: medium;
    padding: 30px 0;
  }

  .navbar {
    height: auto;
  }
  .tabs {
    font-size: x-large;
    padding: 45px 0;
  }
  /* -------- mobile: up to 367px ---------*/
  .h1Library {
    font-size: 20px;
  }
  .h3Library {
    font-size: 17px;
    color: var(--dgreen);
  }
  .npm,
  .hero-body,
  .hero.is-fullheight-with-navbar,
  .hero-body,
  .hero.is-halfheight,
  .hero-body {
    align-items: flex-start;
    display: flex;
    width: 100%;
  }

  .tabicon {
    color: white;
  }
  .tagicon {
    background-color: #294e1d;
  }
  .label {
    font-size: 20px;
  }
  .checkbox {
    font-size: 12px;
  }
  .h2Settings {
    font-size: 17px;
  }
  /* -------- mobile: up to 367px ---------*/
  .inputSettings,
  .input {
    width: 100%;
    height: 45px;
    padding: 6px 12px;
    box-sizing: border-box;
    font-size: 18px;
  }
  .details-button {
    background-color: var(--yellow);
    color: var(--dgreen);
    font-weight: bold;
    font-size: auto;
    width: 50%;
  }
  .save-button {
    background-color: var(--dgreen);
    color: var(--yellow);
    font-weight: bold;
    font-size: auto;
  }

  .details-button:hover {
    background-color: var(--dgreen);
    color: var(--yellow);
    font-weight: bolder;
  }
  .moreButton {
    background-color: var(--yellow);
    color: var(--dgreen);
    font-weight: bold;
    width: 100%;
  }
  .moreButton:hover {
    background-color: var(--dgreen);
    color: var(--yellow);
    font-weight: bolder;
    width: 100%;
  }
  .add-button:hover {
    background-color: var(--dgreen);
    color: var(--yellow);
    font-size: large;
    width: 25%;
  }
  .add-button {
    background-color: var(--yellow);
    color: var(--dgreen);
    width: 25%;
    font-size: large;
  }
  .buttonPantry {
    font-size: 25px;
    font-weight: bold;
  }
}

.tabs {
  background: var(--lgreen);
  padding: 0;
}


.tbody{
    font-size: 1rem;
}
